@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  max-width: 100%;
  position: relative;
  box-sizing: border-box;
}

html,
body {
  position: relative;
  min-height: 100vh;
}

body {
  font-family: "Poppins", sans-serif;
}

.info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 10px;
  text-align: justify;
}

.info > input {
  border: none;
  border-radius: 10px;
  font-size: 15px;
}

.info > * {
  padding: 10px;
}

.btns {
  text-align: center;
}

.btn {
  padding: 10px;
  font-size: 16px;
  margin: 5px;
  background: steelblue;
  color: white;
  font-weight: 600;
  border: none;
  border-radius: 30px;
  outline: none;
  cursor: pointer;
}

.btn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: all 0.3s;
}

.cancel {
  background: transparent;
  font-size: 15px;
  border: 1px solid steelblue;
  color: steelblue;
}

.data {
  font-size: 16px;
  text-align: justify;
  font-weight: 500;
  padding: 10px;
  margin: 10px;
  background: #f3f3f3;
  color: black;
}

body {
  background: #202020;
  color: white;
}

.container {
  text-align: center;
}

h1 {
  padding: 10px;
}

.sections {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 10px;
}

.section {
  padding: 5px 10px;
  margin: 5px;
  text-align: justify;
  background: steelblue;
  color: white;
  font-size: 1.3rem;
  cursor: pointer;
}

.fa-angle-down {
  float: right;
}

#active {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  transition: all 0.3s;
}

#hidden {
  display: none;
}

